body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-raleway: Raleway;
  --font-unbounded: Unbounded;

  /* font sizes */
  --font-size-xl: 1.25rem;
  --font-size-5xl: 1.5rem;
  --font-size-29xl: 3rem;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-darkgray: #989898;
  --color: #cee5d0;
  --border-color: #94b49f;
}
